import React, { Component } from "react";

class Home extends Component{
    constructor(props){
        super(props);
        this.state={
            counter:0,
            quotes:[
                'You know you\'re in love when you cant fall asleep because reality is finally better than your dreams',
                'I’m selfish, impatient and a little insecure. I make mistakes, I am out of control and at times hard to handle. But if you can’t handle me at my worst, then you sure as hell don’t deserve me at my best',
                'The way to get started is to quit talking and begin doing',
                'The first step toward success is taken when you refuse to be a captive of the environment in which you first find yourself',
                'When one door of happiness closes, another opens; but often we look so long at the closed door that we do not see the one which has been opened for us',
                'Twenty years from now you will be more disappointed by the things that you didn’t do than by the ones you did do',
                'When I dare to be powerful – to use my strength in the service of my vision, then it becomes less and less important whether I am afraid',
                ],
            authors:['Dr. Suess','Marilyn Monroe','Walt Disney','Mark Caine','Helen Keller','Mark Twain','Audre Lorde',],
            colors:['#A52A2A','#808080','#D2691E','#20B2AA','#9370DB','#00FF7F','#FF6347','#FFA07A',]
        }
    }

    changeQuote(){
        if(this.state.counter===this.state.quotes.length-1){
            this.setState(state=>({
                counter:0,
            }));
        }else{
            this.setState(state=>({
                counter:state.counter+1,
            }))
        }
    }

    render(){
        return(
            <div class="body" style={{backgroundColor:this.state.colors[this.state.counter]}}>
                <div id="quote-box">
                    <div id="text">
                        <i className="fa fa-quote-left fa-2x left-quote" style={{color:this.state.colors[this.state.counter]}} aria-hidden="true"></i>
                        <h4 className="quote" style={{color:this.state.colors[this.state.counter]}}>{this.state.quotes[this.state.counter]}.</h4>
                        <i className="fa fa-quote-right fa-2x right-quote" style={{color:this.state.colors[this.state.counter]}} aria-hidden="true"></i>
                    </div>
                    <div id="author" style={{color:this.state.colors[this.state.counter]}}>
                        <h6>- {this.state.authors[this.state.counter]}</h6>
                    </div>
                    <div id="buttons">
                        <div>
                            <button id="tweet-quote" style={{backgroundColor:this.state.colors[this.state.counter]}}><a href={`https://twitter.com/intent/tweet?hashtags=quote&text=${this.state.quotes[this.state.counter]}-${this.state.authors[this.state.counter]}`}><i className="fa fa-twitter" aria-hidden="true"></i></a></button>
                        {/* <button id="post-quote" style={{backgroundColor:this.state.colors[this.state.counter]}} ><a><i className="fa fa-facebook" aria-hidden="true"></i></a></button> */}
                        </div>
                        <div>
                            <button id="new-quote" style={{backgroundColor:this.state.colors[this.state.counter]}} onClick={()=>this.changeQuote()}>New Quote</button>
                        </div>
                    </div>                
                </div>
            </div>    
        );
    }
}

export default Home;